import React, {useState} from "react";
import { makeStyles, Table, TableCell, TableHead, TableRow,TablePagination, TableSortLabel } from "@material-ui/core";


const useStyles = makeStyles({
  table: {
    
    '& thead th': {
      fontWeight: '600',
      backgroundColor: '#C9D0D9', 
      opacity: '0.6'
    },
    '& tbody td': {
        fontWeight: '300',
    },
    '& tbody tr:hover': {
        backgroundColor: '#fffbf2',
        cursor: 'pointer',
    },
},
})


function useTable(recods, headCells, filterFn) {
  const TblContainer = (props) => <Table className={classes.table}>{props.children}</Table>;

  const classes = useStyles();

  // pagging 

  const pages = [4, 10, 25]
  const [page, setPage] = useState(0)
  // set roes per page

  const [rowsPerPage, setRowsPerPage] = useState(pages[page])
  const [order, setOrder] = useState()
   const [orderBy, setOrderBy] = useState()

  // table Head

  const TblHead = (props) => {

    const handleSortRequest = cellId => {
      const isAsc = orderBy === cellId && order === "asc";
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(cellId)
  }
    return (
      <TableHead >
        <TableRow>
          {headCells.map((headCell) => (
            <TableCell key={headCell.id}
              sortDirection={orderBy === headCell.id ? order : false}>
              {headCell.disableSorting ? headCell.label :
                <TableSortLabel active={orderBy === headCell.id}
                  direction={orderBy === headCell.id ? order : 'asc'}
                  onClick={() => { handleSortRequest(headCell.id) }}>
                  {headCell.label}
                </TableSortLabel>
              }
            </TableCell>
          ))}
        </TableRow>
      </TableHead>
    );
  };

// handel the page Changes
  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0);
  }

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
}

// initializaing the page in the table 
  
const TblPagination = () => (<TablePagination
  component="div"
  page={page}
  rowsPerPageOptions={pages}
  rowsPerPage={rowsPerPage}
  count={recods.length}
  onChangePage={handleChangePage}
  onChangeRowsPerPage={handleChangeRowsPerPage}
  />)
  
  function stableSort(array, comparator) {
    const stabilizedThis = array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
        const order = comparator(a[0], b[0]);
        if (order !== 0) return order;
        return a[1] - b[1];
    });
    return stabilizedThis.map((el) => el[0]);
}

function getComparator(order, orderBy) {
    return order === 'desc'
        ? (a, b) => descendingComparator(a, b, orderBy)
        : (a, b) => -descendingComparator(a, b, orderBy);
}

function descendingComparator(a, b, orderBy) {
    if (b[orderBy] < a[orderBy]) {
        return -1;
    }
    if (b[orderBy] > a[orderBy]) {
        return 1;
    }
    return 0;
}

const recordsAfterPagingAndSorting = () => {
  return stableSort(filterFn.fn(recods), getComparator(order, orderBy))
            .slice(page * rowsPerPage, (page + 1) * rowsPerPage)
}

  return {
    TblContainer,
    TblHead,
    TblPagination,
    recordsAfterPagingAndSorting
  };
}
export default useTable
